
#preview {
  background-color: white;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  padding: 2em;
  color: #333;
  line-height: 1.2;
}
#preview ul {
    list-style: none;
}
#preview section + section {
    margin-top: 2em;
}
#preview p {
    white-space: pre-line;
}
#preview section > h2:first-child {
    background-color: #ddd;
    display: inline-block;
    padding: .2em;
    margin-bottom: .5em;
}
#preview section[data-name="profile"] > h1 {
    margin: .1em 0;
    font-size: 4em;
}
#preview section[data-name="workHistory"] li + li {
    margin-top: 1em;
}
#preview section[data-name="workHistory"] li h3 {
    border-bottom: 1px solid #999;
    padding-bottom: .3em;
    margin-bottom: .3em;
}
#preview section[data-name="education"] li {
    line-height: 1.5;
}



