
#page {
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background-color: #0099CC;
  font-size: 20px;
}
#page > header {
    width: 100%;
    background-color: white;
    box-shadow: 1px 1px 3px 3px #336699;
}
#page > header #topBar {
      min-width: 1024px;
      max-width: 1440px;
      height: 64px;
      margin: 0 auto;
}
#page main {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    min-width: 1024px;
    max-width: 1440px;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 16px;
    width: 100%;
    margin: 0 auto;
}
#page main #editor {
      min-width: 35%;
      height: 925px;
      margin-right: 16px;
      background-color: white;
      box-shadow: inset 1px 0px 2px 2px lightgray;
}
#page main #preview {
      -webkit-box-flex: 1;
          -ms-flex-positive: 1;
              flex-grow: 1;
      height: 925px;
      background-color: white;
      box-shadow: inset 1px 0px 2px 2px lightgray;
      overflow: scroll;
}
#page .button {
    width: 72px;
    height: 32px;
    border: none;
    cursor: pointer;
    font-size: 18px;
    background-color: #ddd;
    color: #222;
    text-decoration: none;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    vertical-align: middle;
}
#page .button:hover {
      box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.5);
}
#page .button.primary {
      background-color: #02af5f;
      color: white;
}



